<!--  -->
<template>
  <div>
    <el-upload
      class="avatar-uploader"
      :show-file-list="false"
      action=""
      :auto-upload="false"
      :on-change='changeUpload'>
      <img v-if="imageUrl" :src="this.url + '/image/' + imageUrl + '?token=' + token" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <el-dialog title="" :visible.sync="dialogVisible" append-to-body>
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
        <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
            @realTime="realTime"
          ></vueCropper>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="finish" :loading="loading"></el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
// jsjsjson
// import  from '';
// import {getSoundUrl} from '@/http/AppApi'
import {getToken} from '@/utils/LocalDataUtil'
import config from '@/config/index'
export default {
  model: {
    prop: 'imageUrl',
    event: 'updateImgUrl'
  },
  props: {
    imageUrl: {
      type: String
    }
  },
  // import
  components: {},
  data () {
    // 
    return {
      fileName: '',
      url: config.serverUrl(),
      dialogVisible: false,
      token: getToken(),
      previews: {},
      // option
      option: {
        img: '', // 
        info: true, // 
        outputSize: 0.8, // 
        outputType: 'jpeg', // 
        canScale: false, // 
        autoCrop: true, // 
        autoCropWidth: 150, // 
        autoCropHeight: 150, // 
        fixedBox: true, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: true, // 
        canMoveBox: false, // 
        original: false, // 
        centerBox: false, // 
        infoTrue: true // true  false 
      },
      picsList: [], // 
      // 
      loading: false
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    changeUpload (file, fileList) {
      this.fileName = file.name
      let _this = this
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isLt5M) {
        this.$message.error(' 5MB!')
        return false
      }
      // let imageUrl = URL.createObjectURL(file.raw)
      let reader = new FileReader()
      reader.readAsDataURL(file.raw)
      reader.onload = function (event) {
        let imageUrl = this.result
        // _this.$emit('updateImgUrl', imageUrl)
        _this.fileinfo = file
        _this.dialogVisible = true
        _this.option.img = imageUrl
      }

      // 

      setTimeout(() => {
      }, 1000)
    },
    // 
    realTime (data) {
      this.previews = data
    },
    finish () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      var formData = new FormData()
      this.$refs.cropper.getCropBlob(res => {
        // resbolb
        // formData.append('file', res, this.userId)
        // res = res + this.postfix
        formData.append('files', res, this.fileName)
        // getSoundUrl(formData).then(resp => {
        //   if (resp.code && resp.code === '200') {
        //     let data = resp.data
        //     this.$emit('updateImgUrl', data[0])
        //     this.dialogVisible = false
        //     loading.close()
        //   }
        // })
      })
    }
  },
  //  - this
  created () {

  },
  //  - DOM
  mounted () {

  },
  beforeCreate () {}, //  - 
  beforeMount () {}, //  - 
  beforeUpdate () {}, //  - 
  updated () {}, //  - 
  beforeDestroy () {}, //  - 
  destroyed () {}, //  - 
  activated () {} // keep-alive
}
</script>
<style scoped>
.avatar-uploader >>> .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader >>> .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .cropper-content .cropper {
      width: auto;
      height: 400px;
}

</style>



