export default {
  response2Message (resp) {
    let message = null
    let code = resp.code
    if (code) {
      switch (code) {
        case '200':
          break
        case '400':
          message = ''
          break
        case '403':
          message = ''
          break
        case '404':
          message = ','
          break
        case '405':
          message = ','
          break
        case '500':
          message = ','
          break
        case '503':
          message = ','
          break
        default:
          message = resp.message
          break
      }
    }
    return message
  }
}



